
.App {
	display: flex;
	width: 100%;
	height: 400px;
}
.App-form {
	width: 30%;
	height: 100%;
	margin-right: 3px;
}

.characterfield{
	width: 100%;
	height: 30%;
	display: flex;
}

.subfield{
	width: 100%;
	height: 50%;
	display: flex;
}

.kemo{
  width: 100%;
  height: 20%;
  border: 1px solid black;
  margin-bottom: -1px;
  margin-right: -1px;
}
.accessoryfield{
	width: 50%;
	height: 100%;
	display: flex;
  flex-direction: column;
}
.accessory{
  width: 100%;
  height: 25%;
  border: 1px solid black;
  margin-bottom: -1px;
  margin-right: -1px;
}

.groupBadgeStyles{
	background-color: #EBECF0;
	border-radius: 2em;
	color: #172B4D;
	display: inline-block;
	font-size: 12px;
	font-weight: normal;
	line-height: 1;
	min-width: 1;
	padding: 0.16666666666667em 0.5em;
	text-align: center;
}
.groupStyles{
  display: flex;
  align-items: center;
  justify-content: space-between;
};

.userSelect {
    margin: 10rem;
}
.selected {
    padding: 1rem;
}
.characterImg{
	max-height:100%;
	max-width:100%;
	
}






